<template>
<div>
    <md-empty-state v-if="error" v-show="error"
        :md-icon="error.icon"
        :md-label="error.title"
        :md-description="error.description">
        <md-button class="md-primary md-raised" @click="$router.push(error.link)">{{error.link_text}}</md-button>
    </md-empty-state>
    <div v-else>
        <md-progress-spinner v-if="loading" class="md-accent" :md-stroke="3" md-mode="indeterminate"></md-progress-spinner>
        <div v-else-if="order" >
            <md-card class="order-details-card">
                <md-card-header>
                    <div class="md-title">  </div>
                </md-card-header>

                <md-card-content>
                    <div class="flex-list">
                        <div class="md-subheading left-item">
                            : 
                        </div>

                        <div class="md-body-2 right-item">
                            {{order.client.name}} {{order.client.surname}}
                            <br>
                            <md-icon>phone</md-icon>{{order.client.phone}}
                            <br>
                            {{order.address}}
                        </div>
                    </div>

                    <div class="flex-list">
                        <div class="md-subheading left-item">
                             : 
                        </div>

                        <div class="md-body-2 right-item">
                            {{categoryName(order.category)}}
                        </div>
                    </div>

                    <div class="flex-list">
                        <div class="md-subheading left-item">
                              : 
                        </div>

                        <div class="md-body-2 right-item">
                            <md-icon>date_range</md-icon>
                            {{formatDate(order.visit_time)}}
                        </div>
                    </div>

                    <div class="flex-list">
                        <div class="md-subheading left-item">
                              : 
                        </div>

                        <div class="md-body-2 right-item">
                            <md-icon>date_range</md-icon>
                            {{formatDate(order.beginning_time)}}
                        </div>
                    </div>

                    <div class="flex-list">
                        <div class="md-subheading left-item">
                            : 
                        </div>

                        <div class="md-body-2 right-item" :style="statusColour">
                            {{statusName}}
                        </div>
                    </div>

                    <md-divider></md-divider>

                    <div class="flex-list">
                        <div class="md-subheading left-item">
                             : 
                        </div>

                        <div class="md-body-2 right-item">
                            {{order.diagnostic_price}}
                        </div>
                    </div>

                    <div class="flex-list">
                        <div class="md-subheading left-item">
                             : 
                        </div>

                        <div class="md-body-2 right-item">
                            {{order.price}}
                        </div>
                    </div>
                </md-card-content>

                <md-card-actions>
                    <md-button class="md-raised md-primary" v-if="canReview" @click="reviewHandler"> </md-button>
                    <md-button class="md-raised md-accent" v-if="canDecline" @click="declineHandler"></md-button>
                    <md-button class="md-raised md-primary" v-if="canAccept" @click="acceptHandler">{{acceptText}}</md-button>
                    <md-button class="md-raised md-primary" v-show="canChangePrice" @click="priceHandler"> </md-button>
                </md-card-actions>
            </md-card>

            <md-dialog-prompt
            :md-active.sync="show_price_dialog"
            v-model="order.price"
            type="number"
            md-title="  ."
            md-confirm-text="" 
            md-cancel-text=""
            @md-confirm="changePrice"/>
        </div>
    </div>

    <md-dialog-confirm
        :md-active.sync="show_decline_dialog"
        md-title="    ?"
        md-content="  ."
        md-confirm-text=""
        md-cancel-text=""
        @md-confirm="changeStatus" />

    <md-dialog-confirm
        :md-active.sync="show_accept_dialog"
        md-title="   ?"
        md-confirm-text=""
        md-cancel-text=""
        @md-confirm="changeStatus" />

    <md-snackbar md-theme="default" md-position="center" :md-duration="4000" :md-active.sync="show_snackbar" md-persistent>
        <span>{{snackbar_message}}</span>
        <md-button class="md-accent" @click="show_snackbar = false"></md-button>
    </md-snackbar>
</div>
</template>

<script>
import api from '@/API'
import auth from '@/auth'
import {EventBus} from '@/EventBus'

export default {
    name: 'order-detail',
    data() {
        return {
            user: auth.user,
            order: null,
            categories: [],
            error: null,
            loading: false,
            show_snackbar: false,
            snackbar_message: '',
            show_decline_dialog: false,
            show_accept_dialog: false,
            show_price_dialog: false,
            new_status: 0
        }
    },
    async created() {
        EventBus.$on('user-sign-in', this.loadOrderDetails);
        EventBus.$on('user-sign-out', this.signOutListener);

        if (!this.user.is_authenticated) {
            this.showAuthError();
            return;
        };

        await this.loadOrderDetails();
    },
    destroyed() {
        EventBus.$off('user-sign-in', this.loadOrderDetails);
        EventBus.$off('user-sign-out', this.signOutListener);
    },
    computed: {
        acceptText() {
            const statuses = {
                0: ' ',
                1: ' ',
                2: '  ',
                3: ' '
            };

            return statuses[this.order.status];
        },

        canReview() {
            return this.order.client.id === this.user.id && this.order.status === 4;
        },

        canChangePrice() {
            return auth.userIsWorker() && [2, 3].includes(this.order.status);
        },

        canAccept() {
            return auth.userIsWorker() && [0, 1, 2, 3].includes(this.order.status);
        },

        canDecline() {
            if (auth.userIsClient() && [0, 1].includes(this.order.status)) {
                return true;
            }

            if (auth.userIsWorker() && [0, 1, 2, 3].includes(this.order.status)) {
                return true;
            }

            return false;
        },

        statusColour() {
            const colours = {
                0: 'black',
                1: 'black',
                2: 'blue',
                3: 'blue',
                4: 'green',
                5: 'red',
                6: 'red'
            };

            return { color: colours[this.order.status]};
        },

        statusName() {
            const names = {
                0: ' ',
                1: ' ',
                2: '',
                3: '  ',
                4: '',
                5: '',
                6: ''
            };

            return names[this.order.status];
        }
    },
    methods: {
        signOutListener() {
            this.$router.push('/');
        },

        reviewHandler() {
            this.$router.push(`/orders/${this.order.id}/review`);
        },

        acceptHandler() {
            if (this.order.status in [0, 1, 2, 3]) {
                this.new_status = this.order.status + 1;
            };

            this.show_accept_dialog = true;
        },

        declineHandler() {
            if (auth.userIsWorker()) {
                this.new_status = 6;
            };

            if (auth.userIsClient()) {
                this.new_status = 5;
            }

            this.show_decline_dialog = true;
        },

        priceHandler() {
            this.show_price_dialog = true;
        },

        formatDate(date) {
            const now = new Date();
            const new_date = new Date(date);

            const options = {
                month: 'long',
                day: 'numeric',
                year: 'numeric'
            };
            
            return new_date.toLocaleString("ru", options);
        },

        categoryName(id) {
            return this.categories.find(c => c.id == id).description;
        },

        showAuthError() {
            this.error = {
                title: " ",
                description: "       .     ,       .",
                link: "/sign_up",
                link_text: "",
                icon: "lock"
            };
        },

        handleStatus(status) {
            if (status === 502 || status === 504) {
                    this.snackbar_message = '   ';
                    this.show_snackbar = true;
                    return;
                }

                if (status === 401 || status === 403) {
                    this.snackbar_message = ' ';
                    this.show_snackbar = true;
                    return;
                }

                if (status === 404) {
                    this.snackbar_message = '  ';
                    this.show_snackbar = true;
                    return;
                }

                if (status === 400) {
                    this.snackbar_message = '  ';
                    this.show_snackbar = true;
                    return;
                }
        },

        async changePrice() {
            try {
                EventBus.$emit('global-loading-start');
                await api.changePrice(this.order.id, this.order.price);
            } catch (error) {
                const status = error.response.status;
                this.handleStatus(status);
            } finally {
                EventBus.$emit('global-loading-finish');
            }
        },

        async changeStatus() {
            try {
                EventBus.$emit('global-loading-start');
                await api.changeOrderStatus(this.order.id, this.new_status);
                this.order.status = this.new_status;
            } catch (error) {
                const status = error.response.status;
                this.handleStatus(status);
            } finally {
                EventBus.$emit('global-loading-finish');
            }
        },

        async loadOrderDetails() {
            try {
                this.error = null;
                this.loading = true;
                this.order = await api.getOrderDetails(this.$route.params.id);
                this.categories = await api.getCategories();
            } catch (error) {
                const status = error.response.status;

                if (status === 502 || status === 504) {
                    this.snackbar_message = '   ';
                    this.show_snackbar = true;
                    this.error = {
                        title: "   ",
                        description: "     .        .",
                        link: "/",
                        link_text: " ",
                        icon: "error"
                    };
                    return;
                }

                if (status === 401 || status === 403) {
                    this.snackbar_message = ' ';
                    this.show_snackbar = true;
                    this.showAuthError();

                    return;
                }

                if (status === 404) {
                    this.error = {
                        title: "    ",
                        description: "      . ,   ,    .",
                        link: "/orders",
                        link_text: " ",
                        icon: "search"
                    };
                    return;
                }
            } finally {
                this.loading = false;
            }
        }
    }
}
</script>

<style scoped>
.order-details-card {
    width: 60%;
    margin: 0 auto;
}

.flex-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 70%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.left-item {
    flex-basis: 50%;
    text-align: left;
}

.right-item {
    flex-basis: 50%;
    text-align: right;
}
</style>



